import React from 'react'
import { VerticalTimeline, VerticalTimelineElement } from 'react-vertical-timeline-component'
import 'react-vertical-timeline-component/style.min.css';
import '../styles/Experience.css'


const Experience = () => {
  return (
    <div className='experience'>
        <VerticalTimeline lineColor='#3e497a'>
            <VerticalTimelineElement 
            className='vertical-timeline-element--education'
            date="2010 - 2014"
            iconStyle={{background:"#3F3B6C" , color:"#fff"}}
            icon={<box-icon name='graduation' type='solid' ></box-icon>}
            >
                <h3 className='vertical-timeline-element-title'>
                University of Guilan
                </h3>
                <p>Bachelor's degree, Animal Nutrition SciencesBachelor's degree, Animal Nutrition Sciences</p>
            </VerticalTimelineElement>

            <VerticalTimelineElement 
            className='vertical-timeline-element--education'
            date="2017 - 2019"
            iconStyle={{background:"#624F82" , color:"#fff"}}
            icon={<box-icon name='graduation' type='solid' ></box-icon>}
            >
                <h3 className='vertical-timeline-element-title'>
                Qazvin University of Medical Sciences
                </h3>
                <p>Master's degree, Nutrition SciencesMaster's degree, Nutrition Sciences</p>
            </VerticalTimelineElement>

            <VerticalTimelineElement 
            className='vertical-timeline-element--education'
            date="2019 - 2021"
            iconStyle={{background:"#9F73AB" , color:"#fff"}}
            icon={<box-icon name='briefcase' ></box-icon>}
            >
                <h3 className='vertical-timeline-element-title'>
                    Customers Support and Training Supervisor
                </h3>
                <p>Payrad Smart Solutions</p>
            </VerticalTimelineElement>

            <VerticalTimelineElement 
            className='vertical-timeline-element--education'
            date="2021 - present"
            iconStyle={{background:"#A3C7D6" , color:"#fff"}}
            icon={<box-icon name='briefcase' ></box-icon>}
            >
                <h3 className='vertical-timeline-element-title'>
                Front-end Developer 
                </h3>
                <p>Payrad Smart Solutions</p>
            </VerticalTimelineElement>

            
            
        </VerticalTimeline>
    </div>
  )
}

export default Experience